export default{
	data(){
		return{
			userName:"用户名",
			 background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: false,
			interval: 2000,
			duration: 500,
			ischeck:false,
			isLogin:false,
			isShow:false,
			avatarUrl:'',
			userInfo:{},
		}
	},
	methods:{
		bindGetUserInfo(val){
			console.log("获取微信用户数据")
			console.log(val)
			let that=this
			wx.login({
				success:(e)=>{
					console.log('获取code')
					console.log(e)
					let code=e.code
					wx.getUserInfo({
						success:(res)=>{
							console.log("获取用户信息")
							// 获取头像
							console.log(res.userInfo.avatarUrl)	
							this.avatarUrl=res.userInfo.avatarUrl
							// uni.setStorageSync('userAvatar',res.userInfo.avatarUrl)
						}
					})
					that.$http.post("puparent/wxGrant",{
						type:0,
						code:code,
						parentId:''
					}).then(res=>{
						if(res.code==300){								
							uni.navigateTo({
								url:"login?avatar="+this.avatarUrl+"&openid="+res.info.openId
							})
						}else if(res.code==100){
							uni.showToast({
								icon:"none",
								title:"登录成功！"
							})
						}
					})
				}
			})
		},
		getUserList(){
			//获取孩子信息，
			this.$http.post("puchildren/listChildrenLineByParentId",{
				parentId:1,//家长id
			}).then(res=>{
				if(res.code==100){
					
				}
			})
			// 家长组信息
			this.$http.post("puchildren/listChildrenLineByParentId",{
				parentId:1,//家长id
			}).then(res=>{
				if(res.code==100){
					
				}
			})
		},
		lookInfo(){
			//个人资料
			uni.navigateTo({
				url:"../../components/msg/userinfo"
			})
		},
		switch1Change(val){
			// console.log(val.detail.value)
			this.ischeck=val.detail.value
		},
		goEdit(val){
			if(val==1){
				//编辑孩子信息
				uni.navigateTo({
					url:"../../components/information/editchild"
				})
			}else{
				//编辑家长信息
				uni.navigateTo({
					url:"../../components/information/editparent?id="+1
				})
			}
		},
		addChild(index){
			if(index==1){
				uni.navigateTo({
					url:"../../components/information/addchildschool"
				})					
			}else{
				uni.navigateTo({
					url:"../../components/information/editparent"
				})	
			}
		},
		showDrawer(index){
			// console.log("三生三世")
			if(index==1){
				this.isShow=true
			}else{
				this.isShow=false
			}
		},	
		goInfo(index){
			switch(index){
				case 1:
					console.log("快速续费")
					uni.navigateTo({
						url:"../../components/info/renew"
					})
					break
				case 2:
					console.log("安全报告")
					break
				case 3:
					console.log("邀请奖励")
					uni.navigateTo({
						url:"../../components/share/index"
					})
					break
			}
		}
	}
}
